<template>
  <div>
    <div class="line"></div>
    <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#4595F4"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item index="1"
        @click="toHome">首页
      </el-menu-item>
      <el-submenu index="2">
        <template
          slot="title">我的单词</template>
        <el-menu-item
          index="2-1"
          @click="getWordsList">
          今日单词列表
        </el-menu-item>
        <el-menu-item
          index="2-2"
          @click="allWordsList">
          总单词列表
        </el-menu-item>
        <el-menu-item
          index="2-3"
          @click="showCalender">
          数据统计
        </el-menu-item>
        <el-menu-item
          index="2-4"
          @click="markedWords">
          被标记的单词
        </el-menu-item>
        <el-submenu
          index="2-5">
          <template
            slot="title">已过30天的单词</template>
          <el-menu-item
            index="2-5-1"
            @click="allFinishedWords"
            >全部
          </el-menu-item>
          <el-menu-item
            index="2-5-2"
            @click="finishedMarkedWords"
            >
            被标记的单词
          </el-menu-item>
          <el-menu-item
            index="2-5-3">待定
          </el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="3"
        @click="toIntroduction">
        简介</el-menu-item>

    </el-menu>
  </div>

</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      activeIndex: '1',
      activeIndex2: '1',
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath)
    },
    toIntroduction() {
      this.$router.push('/pc/introduction')
    },
    toHome() {
      this.$router.push('/pc')
    },
    getWordsList() {
      this.$router.push('/pc/wordList')
    },
    showCalender() {
      this.$router.push('/pc/calender')
    },
    allWordsList() {
      this.$router.push('/pc/allWordList')
    },
    /**
     * 超过30天的数据
     */
    allFinishedWords() {
      axios({
        method: 'post',
        url: this.$baseURL+'/word/allFinishedWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/pc/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })
    },
    /**
     * 超过30天且被标记的数据
     */
    finishedMarkedWords(){
      axios({
        method: 'post',
        url: this.$baseURL+'/word/finishedMarkedWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/pc/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })

    },
    markedWords(){
      axios({
        method: 'post',
        url: this.$baseURL+'/word/markedWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/pc/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })

    }
    
  },
}
</script>

<style>
.el-menu {
}
</style>